import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export default class PostList extends Component {
    constructor(props) {
        super(props)
        this.state = {
            posts: [],
            index: 0
        }
        this.apis = ['posts', 'albums', 'todos']
    }

    componentDidMount() {
        this.handleFetch()
        setInterval(() => {
            this.handleFetch()
        }, 3000)
    }
    
    handleFetch() {
        fetch('https://jsonplaceholder.typicode.com/' + this.apis[this.state.index])
            .then(res => res.json()).then(posts => this.setState({ posts: posts.slice(0, 10), index: this.state.index == this.apis.length - 1 ? 0 : this.state.index + 1 }))
    }

    render() {
        return <div>
            <h1>{this.apis[this.state.index]}</h1>
            <ul>
                {this.state.posts.map(p => <Link to={`/${p.id}`}><li>{p.title}</li></Link>)}
            </ul>
        </div>
    }
}